<template>
  <div>
    <div class='quter'>
      <ve-line :data="SumData" :title="sumTitle"></ve-line>
    </div>
    <div class='quter'>
      <ve-histogram  :data="customerData" :extend="customerExtend" :settings="customerSettings"></ve-histogram>
    </div>
    <div class='quter'>
      <ve-line :data="SumLocalData" :title="sumLocalTitle"></ve-line>
    </div>
    <div class='quter'>
      <ve-histogram  :data="customerLocalData" :extend="customerLocalExtend" :settings="customerLocalSettings"></ve-histogram>
    </div>
    <div id="import" @click="import19"><a href="javascript:void(0)">{{import_text}}</a><input ref="filElem" type="file" @change="get_file" /></div>
  </div>
</template>

<script>

var HOST = "172.0.0.233/gensmo/";
Date.prototype.Format = function(fmt)   
{ //author: meizz   
  var o = {   
    "M+" : this.getMonth()+1,                 //月份   
    "d+" : this.getDate(),                    //日   
    "h+" : this.getHours(),                   //小时   
    "m+" : this.getMinutes(),                 //分   
    "s+" : this.getSeconds(),                 //秒   
    "q+" : Math.floor((this.getMonth()+3)/3), //季度   
    "S"  : this.getMilliseconds()             //毫秒   
  };   
  if(/(y+)/.test(fmt))   
    fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length));   
  for(var k in o)   
    if(new RegExp("("+ k +")").test(fmt))   
  fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));   
  return fmt;   
}

export default {
  name: 'Index',
  data(){
    return{
      sumTitle:{
        show:true,
        text:'19故障量分析'
      },
      SumData:{
        columns:['日期','故障量','责任故障量'],
        rows:[
        ]
      },
      sumLocalTitle:{
        show:true,
        text:'本地故障量分析'
      },
      SumLocalData:{
        columns:['日期','故障量','责任故障量'],
        rows:[
        ]
      },
      customerExtend:{
        title:{
          show:true,
          text:'19客户故障量排行'
        },
        xAxis:{
          axisLabel:{
            rotate:-60,
            formatter:function(value){
              let txt = '';
              if(value.length > 8){
                txt = value.substring(0,8)+'...';
              }else{
                txt = value;
              }
              return txt;
            }
          }
        }
      },
      customerSettings:{
        stack:{
          '故障量':['责任故障','非责任故障']
        },
      },
      customerData:{
        columns:['客户名称','责任故障','非责任故障'],
        rows:[
        ]
      },
      customerLocalExtend:{
        title:{
          show:true,
          text:'本地客户故障量排行'
        },
        xAxis:{
          axisLabel:{
            rotate:-60,
            formatter:function(value){
              let txt = '';
              if(value.length > 8){
                txt = value.substring(0,8)+'...';
              }else{
                txt = value;
              }
              return txt;
            }
          }
        }
      },
      customerLocalSettings:{
        stack:{
          '故障量':['责任故障','非责任故障']
        },
      },
      customerLocalData:{
        columns:['客户名称','责任故障','非责任故障'],
        rows:[
        ]
      },
      rawData:[],
      rawLocalData:[],
      sum:0,
      importFlag:false,
      import_text:'导入19数据（请选择“沃运维”系统导出的故障单列表）'
    }
  },
  created:function(){
    this.init();
  },
  methods:{
    init:function(){
      let start = new Date();
      let end = new Date();
      start.setDate(1);
      start.setHours(0);
      start.setMinutes(0);
      start.setSeconds(0);
      this.getData(start.Format('yyyy-MM-dd hh:mm:ss'),end.Format('yyyy-MM-dd hh:mm:ss'))
    },
    getData:function(start,end){
      let self = this;
      let data = new FormData();
      data.append('START',start);
      data.append('END',end);
      this.axios
        .post('http://'+HOST+'scripts/assess_order/get_order_by_datetime.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            self.rawData = res.data.result;
            self.sum = res.data.sum;

            //console.log(res.data.result);

            self.getSum();
            self.getCustomer();
          }else{
            alert(res.data.errMsg);
          }
        })
        .catch(function(err){
          alert(err);
        });

      let localData = new FormData();
      localData.append('id','');
      localData.append('name','');
      localData.append('start_time_start','');
      localData.append('start_time_end','');
      localData.append('end_time_start',start);
      localData.append('end_time_end',end);
      localData.append('number','');
      localData.append('index','');
      localData.append('limit','');
      this.axios
        .post('http://'+HOST+'scripts/getList.php',localData)
        .then(function(res){
          if(res.data.status == 'success'){
            self.rawLocalData = res.data.result;

            //console.log(res.data.result);

            self.getLocalSum();
            self.getLocalCustomer();
          }else{
            alert(res.data.errMsg);
          }
        })
        .catch(function(err){
          alert(err);
        })
    },
    getSum(){
      let dateArr = [];
      let row = [];
      let now = new Date();
      now.setHours(0);
      now.setMinutes(0);
      now.setSeconds(0);
      let dayNow = now.getDate();
      let monthNow = now.getMonth()+1;
      for(let i=0;i<dayNow+1;i++){
        dateArr.push(new Date(now.setDate(i+1)));
        row.push({'日期':monthNow+'月'+(i+1)+'日','故障量':0,'责任故障量':0})
      }
      row.pop();

      for(let x in this.rawData){
        let data = this.rawData[x];
        if(this.rawData[x].province != '广东省广州市'){
          continue;
        }
        let time = new Date(data.end_time);
        for(let i=0;i<dayNow;i++){
          if(time > dateArr[i] && time < dateArr[i+1]){
            row[i]['故障量'] = row[i]['故障量'] + 1;
            if(data.is_assess == '1'){
              row[i]['责任故障量'] = row[i]['责任故障量'] + 1;
            }
            break;
          }
        }
      }
      this.SumData.rows = row;
    },
    getCustomer(){
      let row = [];
      let customer = [];
      for(let i in this.rawData){
        if(this.rawData[i].province != '广东省广州市'){
          continue;
        }
        let name = this.rawData[i].name;
        let flag = false;
        for(let j in customer){
          if(customer[j]['客户名称']== name){
            if(this.rawData[i].is_assess == '1'){
              customer[j]['责任故障'] = customer[j]['责任故障']+1;
            }else{
              customer[j]['非责任故障'] = customer[j]['非责任故障']+1;
            }
            customer[j].sum = customer[j].sum + 1;
            flag = true;
            break;
          }
        }
        if(!flag){
          let newCustomer = {
            '客户名称':name,
            '责任故障':0,
            '非责任故障':0,
            sum:1
          }
          if(this.rawData[i].is_assess == '1'){
            newCustomer['责任故障'] = 1;
          }else{
            newCustomer['非责任故障'] = 1;
          }
          customer.push(newCustomer);
        }
      }
      customer.sort(function(v1,v2){
        return v1['sum']-v2['sum'];
      })
      customer.reverse();
      if(customer.length > 15){
        this.customerData.rows = customer.slice(0,15);
      }else{
        this.customerData.rows = customer;
      }     
    },
    getLocalSum(){
      let dateArr = [];
      let row = [];
      let now = new Date();
      now.setHours(0);
      now.setMinutes(0);
      now.setSeconds(0);
      let dayNow = now.getDate();
      let monthNow = now.getMonth()+1;
      for(let i=0;i<dayNow+1;i++){
        dateArr.push(new Date(now.setDate(i+1)));
        row.push({'日期':monthNow+'月'+(i+1)+'日','故障量':0,'责任故障量':0})
      }
      row.pop();

      for(let x in this.rawLocalData){
        let data = this.rawLocalData[x];
        let time = new Date(data.end_time);
        for(let i=0;i<dayNow;i++){
          if(time > dateArr[i] && time < dateArr[i+1]){
            row[i]['故障量'] = row[i]['故障量'] + 1;
            if(data.is_trouble == '1'){
              row[i]['责任故障量'] = row[i]['责任故障量'] + 1;
            }
            break;
          }
        }
      }
      this.SumLocalData.rows = row;
    },
    getLocalCustomer(){
      let row = [];
      let customer = [];
      for(let i in this.rawLocalData){
        let name = this.rawLocalData[i].name;
        let flag = false;
        for(let j in customer){
          if(customer[j]['客户名称']== name){
            if(this.rawLocalData[i].is_trouble == '1'){
              customer[j]['责任故障'] = customer[j]['责任故障']+1;
            }else{
              customer[j]['非责任故障'] = customer[j]['非责任故障']+1;
            }
            customer[j].sum = customer[j].sum + 1;
            flag = true;
            break;
          }
        }
        if(!flag){
          let newCustomer = {
            '客户名称':name,
            '责任故障':0,
            '非责任故障':0,
            sum:1
          }
          if(this.rawLocalData[i].is_trouble == '1'){
            newCustomer['责任故障'] = 1;
          }else{
            newCustomer['非责任故障'] = 1;
          }
          customer.push(newCustomer);
        }
      }
      customer.sort(function(v1,v2){
        return v1['sum']-v2['sum'];
      })
      customer.reverse();
      if(customer.length > 15){
        this.customerLocalData.rows = customer.slice(0,15);
      }else{
        this.customerLocalData.rows = customer;
      }     
    },
    import19:function(){
      this.$refs.filElem.dispatchEvent(new MouseEvent('click'));
    },
    get_file:function(){
      let self = this;
      if(self.importFlag){
        return;
      }
      self.importFlag = true;
      self.import_text = '导入中，请稍后...'
      if(this.$refs.filElem.value == ""){
        self.importFlag = false;
        return;
      }
      let inputFile = this.$refs.filElem.files[0];
      let data = new FormData();
      data.append("file",inputFile);
      this.axios
        .post('http://'+HOST+'scripts/assess_order/import_from_19.php',data)
        .then(function(res){
          self.importFlag = false;
          self.import_text = '导入19数据（请选择“沃运维”系统导出的故障单列表）'
          if(res.data.status == "success"){
            alert("成功导入"+res.data.sum+"条记录!");
          }else{
            console.log(res);
            alert("错误:"+res.data.errMsg);
          }
          self.$refs.filElem.value = "";
        })
        .catch(function(err){
          console.log(err)
          self.importFlag = false;
          self.import_text = '导入19数据（请选择“沃运维”系统导出的故障单列表）'
          self.$refs.filElem.value = "";
        });
    }
  }
}
</script>

